const DEVICE_SPEC = {
    gio: {
        type: 'hid',
        RAM_ADDR: 0x20004000,
        FLASH_ADDR: 0x2A000,
        PROC_ADDR: 0x2B000,
        SRC_ADDR: 0x2C000
    },
    move: {
        type: 'ble',
        RAM_ADDR: 0x20004000,
        FLASH_ADDR: 0x2A000,
        PROC_ADDR: 0x2B000,
        SRC_ADDR: 0x2C000
    },
    microbit: {
        type: 'serial',
        RAM_ADDR: 0x2001F000,
        FLASH_ADDR: 0x40000,
        PROC_ADDR: 0x41000,
        SRC_ADDR: 0x42000
    }
}

function getDeviceSpec(name) { return DEVICE_SPEC[name] }

export default getDeviceSpec;
