function hexByte(n){return (n+256).toString(16).substring(1);}
function hexWord(n){return ('00000000'+(n+65536*65536).toString(16)).slice(-8);}
function strToBytes(str) {
    var res = [];
    for (let i=0; i<str.length; i++) res.push(str.charCodeAt(i));
    return res;
}

export { hexByte, hexWord, strToBytes };
