import { useContext, useEffect, useState } from 'react';

import CommandCenter from '../../containers/command-center.js';
import EditorComponent from '../editor/editor.js';
// import ProcToolsComponent from '../proc-tools/proc-tools.js';
import { SettingsContext } from '../../contexts/settings.js';

import { readSource } from '../../lib/vm-commands.js';
import Compiler from '../../lib/compiler.js';
// import { strToBytes } from '../../lib/formatting.js';

import './editor-container.css';

const EditorContainer = (props) => {
    const [ sourceCode, setSourceCode ] = useState(null);
    const { settingsState } = useContext(SettingsContext);

    useEffect(() => {
        if (Object.keys(settingsState.info).length > 0 && settingsState.info.type === 'vm') {
            readSource().then(source => {
                console.log(source);
                Compiler.downloadProcs(source);
                setSourceCode(source);
            });
        }
    }, [settingsState.info]);

    // const handleDownload = async () => {
        // const str = props.procRef.current.value;
        // const strBytes = strToBytes(str);
        // const bytes = Compiler.downloadProcs(str);
        // if (!bytes || bytes.length === 0) return;
        // await download(VM_ADDR(), bytes);
        // await saveSource(strBytes);
    // };
    // const handleUpload = async () => {
        // const source = await readSource();
        // props.procRef.current.value = source;
        // Compiler.compileProcs(source);
    // };
    return (
        <div className='editor-container'>
            <CommandCenter
                weight={4}
                side={'left'}
            />
            <EditorComponent
                flex={6}
                side={'right'}
                textRef={props.procRef}
                defaultValue={sourceCode}
            />
        </div>
    );
}

export default EditorContainer;
