const SET_MONITOR = 'setMonitor';

const defaultMonitors = [];
for (let i=0; i<10; i++) defaultMonitors.push({value: 0, timestamp: 0});
const initialState = {
    monitors: defaultMonitors
}

const reducer = function (state, action) {
    if (typeof state === 'undefined') state = initialState;
    switch (action.type) {
    case SET_MONITOR:
        const change = [...state.monitors];
        change[action.mon-1].value = action.val;
        change[action.mon-1].timestamp = Date.now();
        return Object.assign({}, state, {
            monitors: change
        });
    default:
        return state
    }

};

const setMonitor = function (mon, val) {
    return {
        type: SET_MONITOR,
        mon: mon,
        val: val
    }
};

export {
    reducer as default,
    initialState,
    setMonitor
}
