import Comms from './comms.js';
import { hexWord, strToBytes } from './formatting.js';

import getDeviceSpec from './device-spec.js';

// const RAM_ADDR = 0x2001F000;
// const RAM_ADDR = 0x20004000;

function RAM_ADDR () { return getDeviceSpec(Comms.device).RAM_ADDR; }
function FLASH_ADDR () { return getDeviceSpec(Comms.device).FLASH_ADDR; }
function PROC_ADDR () { return getDeviceSpec(Comms.device).PROC_ADDR; }
function SRC_ADDR () { return getDeviceSpec(Comms.device).SRC_ADDR; }

function sendstr (str) {
    // console.log(`Sending ${str}`);
    sendReceive(str, 0);
}

async function writeBin(bin) {
    console.log(`[1 of 4] writing ${(bin.length/1000).toFixed(1)}KB to memory`);
    const start = Date.now();
    let time = Date.now();
    await writeMemory(RAM_ADDR(), bin);
    const ramTime = Date.now() - time;
    console.log('[2 of 4] copying to flash');
    time = Date.now();
    await Comms.sendAndWait(strToBytes(`f1A000,${hexWord(RAM_ADDR())},${hexWord(bin.length/4)}#`), 0x55);
    const writeTime = Date.now() - time;
    console.log('[3 of 4] reading data from flash');
    time = Date.now();
    const mem = await readWords(0x1A000, bin.length/4);
    const readTime = Date.now() - time;
    console.log('[4 of 4] verifying data');
    if (verifyMemory(bin, mem)) {
        console.log('Success!');
        console.log(`Flash time: ${((Date.now() - start)/1000).toFixed(2)}s`,
                    `Mem write: ${(bin.length/ramTime).toFixed(1)}KBps`,
                    `Flash write: ${(bin.length/writeTime).toFixed(1)}KBps`,
                    `Flash read: ${(bin.length/readTime).toFixed(1)}KBps`);
    } else {
        console.log('Flash failed');
    }
}

async function getVersion() {
    return await sendReceive('v', 6);
}

function resetToBootloader() {
    sendReceive('R', 0);
}

function verifyMemory(src, dst) {
    if (src.length !== dst.length) return false;
    let isValid = true;
    dst.forEach((d, i) => {
        if (d !== src[i]) {
            isValid = false;
            console.log(`verify failed: ${hexWord(i)} - should be: ${hexWord(src[i])} actual: ${hexWord(d)}`);
        }
    });
    return isValid;
}

async function sendReceive(str, n) {
    return new Promise(async res => {
        const resp = await Comms.sendAndWaitLen(strToBytes(str), n);
        res(resp);
    });
}

async function writeWord(addr, w) {
    // console.log(`Writing word ${hexWord(w)} at ${hexWord(addr)}`);
    if (Comms.type === 'serial' || Comms.type === 'ble') {
        await sendReceive(`W${hexWord(addr)},${hexWord(w)}#`, 1);
    } else {
        sendstr(`W${hexWord(addr)},${hexWord(w)}#`);
    }
}

async function readWord(addr) {
    return new Promise(async resolve => {
        const res = await sendReceive(`w${hexWord(addr)}#`, 4);
        resolve(res);
    });
}

async function readWords(addr, len) {
    return new Promise(async resolve => {
        const res = await sendReceive(`R${hexWord(addr)},${hexWord(len)}#`, len*4);
        resolve(res);
    });
}

async function writeMemory (addr, mem) {
    while (mem.length%4>0) mem.push(0);
    var bytes = new Uint8Array(mem);
    var data = new DataView(bytes.buffer);
    for (var i=0; i<bytes.length; i+=4) {
        await writeWord(addr+i, data.getUint32(i, true));
    }
}

async function readMemory(addr, len) {
    let res = [];
    for (let i=0; res.length<len; i+=4) {
        res = res.concat(await readWord(addr));
        addr += 4;
    }
    return res.slice(0, len);
}

async function readMemoryWord(addr, len) {
    let res = [];
    for (let i=0; res.length<len; i+=4) {
        let num = 0;
        const r = await readWord(addr);
        r.forEach((n, i) => {
            num |= n << (i*8);
        });
        res = res.concat(num);
        addr += 4;
    }
    return res;
}

async function sendCommand (addr, bytes) {
    console.log(bytes);
    await writeMemory(addr, bytes);
    const command = `f${hexWord(FLASH_ADDR())},${hexWord(addr)},${Math.ceil(bytes.length/4)}#`;
    await Comms.sendAndWait(strToBytes(command), 0x55);
}

async function download (bytes) {
    console.log(RAM_ADDR(), PROC_ADDR());
    await writeMemory(RAM_ADDR(), bytes);
    const procAddr = hexWord(PROC_ADDR());
    await Comms.sendAndWait(strToBytes(`f${procAddr},${hexWord(RAM_ADDR())},${hexWord(bytes.length/4)}#`), 0x55);
    console.log('procs written');
}

async function saveSource (bytes) {
    const len = bytes.length;
    bytes.unshift(len&0xFF,(len>>8)&0xFF,(len>>16)&0xFF,(len>>24)&0xFF);
    await writeMemory(RAM_ADDR(), bytes);
    const srcAddr = hexWord(SRC_ADDR());
    const command = `f${srcAddr},${hexWord(RAM_ADDR())},${hexWord((bytes.length+4)/4)}#`;
    await Comms.sendAndWait(strToBytes(command), 0x55);
    console.log('source saved');
}

async function readSource () {
    console.log('reading source');
    let addr = SRC_ADDR();
    return new Promise(async res => {
        const len = await readMemoryWord(addr, 1);
        if (len > 1000) {
            res('');
            return;
        }
        const mem = await readMemory(addr+4, len);
        let source = '';
        mem.forEach(b => {
            if (b === 255 || b === 0) return;
            source += String.fromCharCode(b);
        });
        res(source);
    });
}

function vmGo() {
    sendstr('G');
}

async function vmStop() {
  await runCmd([11]);
}

async function runCmd (code) {
    code.push(0);
    await sendCommand(RAM_ADDR(), code);
    vmGo();
}

function dumpw(addr, len) {
    return new Promise(res => {
        if (len < 1) len = 1;
        readMemoryWord(addr, len)
        .then(mem => res(mem));
    });
}

function dump(addr, len) {
    return new Promise(res => {
        if (len < 4) len = 4;
        readMemory(addr, len)
            .then(mem => res(mem));
    });
}

export {
    RAM_ADDR,
    sendstr,
    writeWord,
    writeMemory,
    readMemory,
    sendCommand,
    runCmd,
    dump,
    dumpw,
    writeBin,
    getVersion,
    resetToBootloader,
    vmGo,
    vmStop,
    download,
    saveSource,
    readSource
};
