import { useContext } from 'react';

import { ModalContext } from '../contexts/modals.js';

import IconButtonComponent from '../components/icon-button/icon-button.js';

import settingsIcon from '../icons/settings.svg';

function SettingsButton() {
    const { modalActions } = useContext(ModalContext);

    function onClick (e) {
        modalActions.setVisible('settings');
    }

    return (
        <IconButtonComponent
            title={'Change editor settings'}
            src={settingsIcon}
            alt={'Settings Icon'}
            type={'settings'}
            handleClick={onClick}
        />
    );
}

export default SettingsButton;
