function useVersionParser(verString) {
    if (verString.length !== 5) {
        console.log(`Error: version string wrong length ${verString}`);
        return;
    }
    const version = {};
    version.type = verString[4] === 'b' ?
        'bootloader' : verString[4] === 'v' ? 'vm' : 'unknown';
    version.major = verString[1];
    version.minor = verString[2];
    version.patch = verString[3];
    return version;
}

export default useVersionParser;
