import './icon-button.css';

function IconButtonComponent(props) {
    let classNames = 'icon-button ';
    classNames += props.connected ? 'connected ' : '';
    classNames += props.spinning ? 'spinning ' : '';
    return (
        <div
            title={props.title}
            className={classNames}
            onClick={props.handleClick}>
            <img
                className={`icon-button-img ${props.type}`}
                src={props.src}
                alt={props.alt} />
        </div>
    );
}

export default IconButtonComponent;
