import { saveSettings } from '../lib/local-settings.js';

const initialState = {
    auto: false,
    deviceType: 'gio',
    connected: false,
    info: {}
};

function reducer(state, action) {
    let newSettings;
    switch(action.type) {
        case 'SET_AUTOCONNECT':
            newSettings = {
                ...state,
                auto: action.auto
            }
            saveSettings(newSettings);
            return newSettings;
        case 'SET_DEVICETYPE':
            newSettings = {
                ...state,
                deviceType: action.deviceType
            }
            saveSettings(newSettings);
            return newSettings;
        case 'SET_CONNECTED':
            return {
                ...state,
                connected: true
            }
        case 'SET_DISCONNECTED':
            return {
                ...state,
                connected: true
            }
        case 'SET_DEVICE_INFO':
            return {
                ...state,
                info: action.info
            }
        case 'CLEAR_DEVICE_INFO':
            return {
                ...state,
                info: {}
            }
        default: return state;
    }
}

export {
    reducer as default,
    initialState
}
