import { useEffect, useContext, createRef } from 'react';

import { ModalContext } from '../../contexts/modals.js';
import { SettingsContext } from '../../contexts/settings.js';

import Comms from '../../lib/comms.js';

import Header from '../../containers/header.js';
import EditorContainer from '../editor-container/editor-container.js';
import FooterComponent from '../footer/footer.js';

import SettingsModal from '../../containers/settings-modal.js';

import './gui.css';

function GUIComponent() {
    const { modalState } = useContext(ModalContext);
    const { settingsState } = useContext(SettingsContext);
    const procRef = createRef();
    useEffect(() => {
        Comms.setType(settingsState.deviceType);
    }, [settingsState]);
    return (
        <div className='gui-frame'>
            {settingsState.auto}
            <div className='gui-header'>
                <Header procRef={procRef} />
            </div>
            <div className='gui-body'>
                <EditorContainer procRef={procRef} />
            </div>
            <div className='gui-footer'>
                <FooterComponent />
            </div>
            {modalState.settings && <SettingsModal />}
        </div>
    );
}

export default GUIComponent;
