import Tokenizer from './tokenizer.js';
import { prims, globals, constants } from './prims.js';
import parse from './parse.js';
import { hexWord, hexByte } from './formatting.js';

class Compiler {

lprint(e) {
    console.log(e);
}

/////////////////////////////////
//
// Entry Points
//
/////////////////////////////////
compile(str){
	this.pc = 0;
    try {
        this.thisproc = undefined;
        var compiledtokens = this.compileCommands(parse(str));
        var code = this.encodeItems(compiledtokens);
        return code;
    } catch (e) {console.log(e);}
    return null;
}

downloadProcs(str){
	this.pc = this.procaddr;
	try {
		this.setup();
		var bytes = this.compileProcs(str);
		compiler.dump(this.procaddr, bytes);
        return bytes;
		// if(comms.port) comms.download(bytes);
 	} catch (e) {console.log(e)}
}


/////////////////////////////////
//
// Vectors
//
/////////////////////////////////

vectors(){
	var t = this;
	var res = [];
	var nextvector = 0;

	for(var i=0;i<t.vectorlen;i++) res.push(0);
	setupVectors('on-start',0);
	return res;

	function setupVectors(type,op){
		setupVector(type,op);
		for(var i=1;i<t.vectorlen/4;i++) setupVector(type+i,op);
	}

	function setupVector(name, op){
		var proc = t.oblist[name];
		if(proc===undefined) return;
		var addr = proc.addr;
		res[nextvector++] = op //EVAL_PROC
		res[nextvector++] = addr&0xff;
		res[nextvector++] = (addr>>8)&0xff;
		nextvector++;
	}
}


/////////////////////////////////
// 
// Procedure Compiler
//
/////////////////////////////////

compileProcs(str){
	var t = this;
	var tk = new Tokenizer(str);
	while(!tk.eof()) onePass1Command();
	for(var i in t.procnames) compileProcBody(t.procnames[i]);
	return t.encodeProcs();

	function onePass1Command(){
		var sol = tk.offset;
		var parsed = parse(tk.nextLine());
		if(parsed.length===0) return;
			if(parsed[0]==='to') parseTo();
			else if(parsed[0]==='constants') parseConstants();
			else if(parsed[0]==='global') parseGlobal();
		
		function parseTo(){
			var name = parsed[1];
			var proc = {};
			parseTitleLine();
			proc.type = 'ufun';
			proc.len = 3;
			proc.nargs = parsed.length-2;
			proc.body = gatherBody();
			proc.outputs = proc.body.match(/(^|\W)output\W/)!=null;
			t.oblist[name] = proc;
			t.procnames.push(name);

			function parseTitleLine(){
				if(parsed.length===1) throw new Error('"to" needs to be followed by a name');
				if((typeof parsed[1])!='string') throw new Error(parsed[1]+' can\'t be used as a name');
				proc.args = [];
				proc.locals = [];
				var args = parsed.slice(2);
				for(var i in args){
					var arg = args[i];
					if((typeof arg)!='string') throw new Error('"'+arg.toString().replace(',',' ')+'" can\'t be used as an input name');
					if(arg[0]!==':') throw new Error('"'+arg+'" needs a ":" to be an input');
					t.oblist[arg] = {type: 'getlocal', outputs: true};
					t.oblist['set'+arg] = {type: 'setlocal', outputs: false};
					proc.args.push(arg); 
				}
			}

			function gatherBody(){
				var res = '';
				while(true){
					if(tk.eof()) return res;
					var line = tk.nextLine();
					if(line==='end') return res;
					res = res+line+'\n';
				}
			}
		}
		
		function parseGlobal(){
			backup();
			var globals = tk.readToken();
			for(var i in globals) t.setupGlobal(globals[i]);
		}
				
		function parseConstants(){
			backup();
			var ks = tk.readToken();
			for(var i in ks) t.constants[ks[i][0]]=ks[i][1];
		}

		function backup(){tk.offset=sol; tk.readToken();}
	}
	
	function compileProcBody(name){
		var proc = t.oblist[name];
		t.thisproc = name;
		proc.addr = t.pc;
		t.pc += 2;
		proc.items = t.compileCommands(parse(proc.body+'stop\n'));
	}

}

	
/////////////////////////////////
// 
// Command Compiler
//
/////////////////////////////////

compileCommands(list){
	var t = this;
	var res = [];
	while(list.length>0) compileItem(list.shift());
	return res;

	function compileItem(item, inputfor){
		var type = typeof item;
		if (type==='number') compileNumber(item);
		else if(type==='object') compileList(item);
		else if(item.substring(0,1)==="'") compileString(item);
		else if(item.substring(0,1)===':') compileLocalGet(item);
		else if(item.substring(0,4)==='set:') compileLocalSet(item);
		else compileSymbol(item);
		var command = (t.oblist[item])&&!t.oblist[item].outputs;
		if((!command)&&(inputfor===undefined)) throw new Error('you don\'t say what to do with '+item+thisProc());
		if(inputfor&&command) throw new Error(item+' doesn\'t output to '+inputfor+thisProc());
	}

	function compileNumber(n){
		if((n<256)&&(n>=0)&&(n===Math.round(n))) addAndCount(['byte',n],2);
		else addAndCount(['number',n],5)
	}

	function compileList(l){
		addAndCount(['list',0],3)
		var oldlist = list;
		list = l;
		while(list.length>0) compileItem(list.shift());
		list = oldlist;
		addAndCount(['eol',0],1)
	}

	function compileString(str){
		str = str.substring(1);
		str = str.substring(0,str.length-1);
		str = str.replaceAll('\\n', '\r');
		str += String.fromCharCode(0);
		addAndCount(['string',str],str.length+3);
	}

	function compileLocalGet(name){
		var index = localIndex(name);
		if(index===undefined) throw new Error(name+' has no value'+thisProc());
		addAndCount(['localget',index],2);
	}

	function compileLocalSet(setname){
		var name = setname.substring(3);
		var index = localIndex(name);
		if(index===undefined) throw new Error(name+' has no value'+thisProc());
		argloop(1, setname);
		addAndCount(['localset',index],2);
	}

	function compileSymbol(name){
		var sym = t.oblist[name];
		if(constants[name]!==undefined) compileNumber(constants[name]);
		else if(sym===undefined) throw new Error(`I don't know how to '${name+thisProc()}'`);
		else if(sym.type==='global') compileGlobalGet(sym.index);
		else if(sym.type==='setglobal') compileGlobalSet(sym.index);
		else if(sym.type==='let') compileLet();
		else if(sym.type==='make') compileMake();
		else if(sym.type==='opar') compileOpar();
		else if(sym.type==='waituntil') compileWaituntil();
		// else if(sym.type=='repeatuntil') compileRepeatuntil();
		else compileCallSym();

		function compileCallSym(){
			argloop(sym.nargs, name);
			addAndCount([sym.type, name], sym.len);
		}

		function compileGlobalSet(index){
			argloop(1, sym);
            const rbox = index>2;
			addAndCount(['globalset',rbox?index-3:index,rbox?1:0],3);
		}

		function compileGlobalGet(index){
            const rbox = index>2;
			addAndCount(['globalget',rbox?index-3:index,rbox?1:0],3);
		}

		function compileWaituntil(){
			addAndCount(['list',0],3)
			var oldlist = list; list = list.shift();
			argloop(1, 'waituntil');
			list = oldlist;
			addAndCount(['eolr',0],1)
			addAndCount(['prim','waituntil'],1);
		}

		function compileLet(){
			var old = list;
			list = old.shift();
			while(list.length>0){
				var local = ':'+list.shift();
				t.oblist[local] = {type: 'getlocal', outputs: true};
				t.oblist['set'+local] = {type: 'setlocal', outputs: false};
				t.oblist[t.thisproc].locals.push(local);
				compileLocalSet('set'+local);
			}
			list = old;
		}

		function compileMake(){
			var name = list.shift();
			if(name.substring(0,1)===':') name=name.substring(1);
			if(name.substring(0,1)==='"') name=name.substring(1);
			name = ':'+name;
			var index = localIndex(name);
			if(index===undefined) throw new Error(name+' has no value'+thisProc());
			argloop(1, 'make');
			addAndCount(['localset',index],2);
		}

		function compileOpar(){
			argloop(1, '(');
			if(!(list.shift()===')')) throw new Error('() error|')
		}

	}

	function argloop(nargs, name){
		for(var i=0;i<nargs;i++) {
			if(list.length===0) throw new Error(name+' needs more inputs'+thisProc());
			compileItem(list.shift(),name);
			infixCheck();
		}
	}

	function infixCheck(){
		var infixnext = (list.length>0)&&(t.oblist[list[0]])&&(t.oblist[list[0]].nargs===-1);
		if(!infixnext) return;
		var isym = list.shift();
		if(list.length===0) throw new Error(isym+' needs more inputs'+thisProc());
		compileItem(list.shift(),isym);
		addAndCount(['prim',isym],1);
		infixCheck();
	}

	function localIndex(name){
		if(t.thisproc===undefined) return undefined;
		var args = t.oblist[t.thisproc].args;
		var index = args.indexOf(name);
		if(index!==-1) return args.length-1-index;
		var locals = t.oblist[t.thisproc].locals;
		index = locals.indexOf(name);
		if(index===-1) return undefined;
		return 0xff&-index-1;
	}

	function thisProc(){
		if(t.thisproc) return ' in '+t.thisproc;
		else return '';
	}

	function addAndCount(x,n){
		res.push([t.pc].concat(x));
		t.pc+=n;
	}


}




/////////////////////////////////
// 
// Generate ByteCodes
//
/////////////////////////////////

encodeItems(items){
	var res = [];
	var lists = [];
	for(var i in items) this.encodeItem(items[i], res, lists);
	return res;
}

encodeProcs(){
	var res = [];
	var lists = [];
	for(var i in this.procnames){
		var proc = this.oblist[this.procnames[i]];
		var items = proc.items;
		res.push(proc.args.length);
		res.push(proc.locals.length);
		for(var n in items) this.encodeItem(items[n], res, lists);
	}
	return res;
}

encodeItem(i, res, lists){
	var val = i[2];
	switch(i[1]){
	case 'byte':
		res.push(1);
		res.push(val&0xff);
		break;
	case 'number':
		res.push(2);
		res.push(val&0xff);
		res.push((val>>8)&0xff);
        res.push((val>>16)&0xff);
        res.push((val>>24)&0xff);
		break;
	case 'list':
		res.push(3);
		lists.push(res.length);
		res.push(0);
		res.push(0);
		break;
	case 'string':
		res.push(3);
		res.push(val.length&0xff);
		res.push((val.length>>8)&0xff);
		val.split('').forEach(function(x){res.push(x.charCodeAt(0));});
		break;
	case 'eol':
		addEOL(4);
		break;
    case 'eolr':
		addEOL(5);
		break;
	case 'localget':
		res.push(6);
		res.push(val);
		break;
	case 'localset':
		res.push(7);
		res.push(val);
		break;
	case 'globalget':
		res.push(8);
		res.push(val);
        res.push(i[3]);
		break;
	case 'globalset':
		res.push(9);
		res.push(val);
        res.push(i[3]);
		break;
	case 'prim':
		res.push(this.oblist[val].code);
		break;
	case 'pseudoprim':
	case 'ufun':
		res.push(10);
		res.push(this.oblist[val].addr&0xff);
		res.push((this.oblist[val].addr>>8)&0xff);
		break;
	default:
		res.push(i[1]);
		break;
	}

	function addEOL(n){
		res.push(n);
		var offset = lists.pop();
		var len = res.length-offset-2;
		res[offset] = len&0xff;
		res[offset+1] = (len>>8)&0xff;
	}
}


/////////////////////////////////
//
// Setup
//
/////////////////////////////////

dumpw(addr,list){
    for (var i=0; i<list.length; i+=4) {
		var line = hexWord(addr+i)+' - ';
		for(var n=0;n<4;n++){
			if((i+n)>=list.length) break;
			line+= hexWord(list[n+i]);
			line+= ' ';
		}
		console.log(line)
	}
}

dump(addr,list){
    for (var i=0; i<list.length; i+=8) {
		var line = hexWord(addr+i)+' - ';
		for(var n=0;n<8;n++){
			if((i+n)>=list.length) break;
			line+= hexByte(list[n+i]);
			line+= ' ';
		}
		console.log(line)
	}
}

setupGlobal(name){
	this.oblist[name] = {type: 'global', index: this.nextglobal, outputs: true};
	this.oblist['set'+name] = {type: 'setglobal', index: this.nextglobal, outputs: false};;
	this.nextglobal++;
}


setup(){
	var t = this;
	this.procaddr = 0;
	t.vectorlen = 4;
	t.oblist = {};
	t.constants = {};
	t.nextglobal = 0;
	t.procnames = [];
	setupBuiltIns();
	t.oblist['let'] = {type: 'let', outputs: false};
	t.oblist['make'] = {type: 'make', outputs: false};
	t.oblist['('] = {type: 'opar', outputs: true};
    t.oblist['waituntil'].type = 'waituntil';
	for(var i in globals) t.setupGlobal(globals[i]);

	function setupBuiltIns(){
		var index = 11;
		var list = [].concat(prims);
		while(list.length>0){
			var name = list.shift();
			var ops = list.shift()==='r';
			var nargs = list.shift();
			t.oblist[name] = {type: 'prim', len: 1, code: index++, nargs: nargs, outputs: ops};
		}
	}

}

}

const compiler = new Compiler();
compiler.setup();
export default compiler;
