import { useReducer, createContext } from 'react';

import settingsReducer from '../reducers/settings.js';

const SettingsContext = createContext();

const initialState = {
    deviceType: 'gio',
    auto: false,
    info: {}
};

function SettingsProvider(props) {
    initialState.auto = props.value.auto;
    initialState.deviceType = props.value.deviceType;
    const [state, dispatch] = useReducer(settingsReducer, initialState);

    const actions = {
        setAutoconnect: auto => {
            dispatch({ type: 'SET_AUTOCONNECT', auto: auto });
        },
        setDeviceType: type => {
            dispatch({ type: 'SET_DEVICETYPE', deviceType: type });
        },
        setConnected: () => {
            dispatch({ type: 'SET_CONNECTED' });
        },
        setDisconnected: () => {
            dispatch({ type: 'SET_DISCONNECTED' });
        },
        setDeviceInfo: dev => {
            dispatch({ type: 'SET_DEVICE_INFO', info: dev });
        },
        clearDeviceInfo: dev => {
            dispatch({ type: 'CLEAR_DEVICE_INFO' });
        }
    };

    return (
        <SettingsContext.Provider
            value={{
                settingsState: state,
                settingsActions: actions
            }}
        >
            {props.children}
        </SettingsContext.Provider>
    );
}

export { SettingsProvider, SettingsContext };
