import { useEffect, useState, useRef } from 'react';
import { connect } from 'react-redux';

import MonitorComponent from '../monitor/monitor.js';

function ToolbarMonitorsComponent(props) {
    const [ isVisible, setIsVisible ] = useState(false);
    const visibleTimeout = useRef(null);
    const firstRun = useRef(true);

    useEffect(() => {
        if (firstRun.current) {
            firstRun.current = false;
            return null;
        }

        setIsVisible(true);
        if (visibleTimeout.current) clearTimeout(visibleTimeout.current);
        visibleTimeout.current = setTimeout(() => {
            setIsVisible(false);
        }, 2000);
    }, [props.monitors]);

    return (
        <>
        {isVisible ?
            props.monitors.map((mon, i) => {
                if (Date.now() - mon.timestamp > 3000) return null
                return <MonitorComponent key={i} num={i+1} val={mon.value} />
            }) : null
        }
        </>
    );
}

const mapStateToProps = state => {
    return {
        monitors: state.monitors
    }
};

export default connect(
    mapStateToProps
)(ToolbarMonitorsComponent);
