import { useContext, useState } from 'react';

import { writeBin, resetToBootloader, vmGo } from '../lib/vm-commands.js';

import SettingsModalComponent from '../components/settings-modal/settings-modal.js';

import { ModalContext } from '../contexts/modals.js';
import { SettingsContext } from '../contexts/settings.js';

function SettingsModal(props) {
    const { settingsState, settingsActions } = useContext(SettingsContext);
    const [isFlashing, setIsFlashing] = useState(false);
    const { modalActions } = useContext(ModalContext);

    function handleClose() {
        modalActions.setHidden('settings');
    }

    function handleSettingsChange(e) {
        const setting = e.target.name;
        switch(setting) {
        case 'autoconnect':
            settingsActions.setAutoconnect(e.target.checked);
            break;
        case 'device-type':
            settingsActions.setDeviceType(e.target.value)
            break;
        default: break;
        }
    }

    function handleReset(type) {
        if (type === 'bootloader') resetToBootloader();
        else if (type === 'vm') vmGo();
    }

    const handleFlashFirmware = (e) => {
        setIsFlashing(true);
        if (e.shiftKey) {
            const input = document.createElement('input');
            input.type = 'file';
            input.oninput = handleUpload;
            input.click();
        } else {
            fetch('https://craftybits.net/firmware/gio/latest')
            .then(res => res.arrayBuffer())
            .then(async buffer => {
                const bin = new Uint8Array(buffer);
                await writeBin(bin);
                setIsFlashing(false);
            });
        }
    };

    function handleUpload (e) {
        const file = e.target.files[0];
        const reader = new FileReader();
        reader.onload = async (e) => {
            const bin = new Uint8Array(e.target.result);
            await writeBin(bin);
            setIsFlashing(false);
        };
        reader.readAsArrayBuffer(file);
    }

    return (
        <SettingsModalComponent
            autoconnect={settingsState.auto}
            deviceType={settingsState.deviceType}
            devInfo={settingsState.info}
            closeSettings={handleClose}
            changeSettings={handleSettingsChange}
            onReset={handleReset}
            onFlashFirmware={handleFlashFirmware}
            isFlashing={isFlashing}
            {...props}
        />
    );
}

export default SettingsModal;
