import Box from '../../containers/box.js';

import './settings-modal.css';

function SettingsModalComponent(props) {
    const devActive = Object.keys(props.devInfo).length > 0;
    const reset = () => {
        const type = props.devInfo.type === 'vm' ? 'bootloader' : 'vm';
        props.onReset(type);
    }
    return (
        <Box onClose={props.closeSettings}>
            <div className='settings-frame'>
                <div className='settings-header'>Settings</div>
                <div className='settings-row'>
                    Device:
                    <select
                        name='device-type'
                        className='settings-device-type'
                        onChange={props.changeSettings}
                        value={props.deviceType}>
                        <option value='gio'>CraftyBits Gio</option>
                        <option value='move'>CraftyBits Move</option>
                        <option value='microbit'>micro:bit</option>
                    </select>
                </div>
                <div className='settings-title'>Device Info</div>
                <div className='settings-row'>
                    Status: {devActive ? 'connected' : 'Not connected'}
                </div>
                { devActive &&
                    <div className='settings-dev-info'>
                        <div className='settings-row'>
                            mode: {props.devInfo.type}
                        </div>
                        <div className='settings-row'>
                            version: {props.devInfo.major}.{props.devInfo.minor}.{props.devInfo.patch}
                        </div>
                        <button
                            className='settings-button'
                            disabled={props.isFlashing}>
                            Disconnect
                        </button>
                        <br />
                        { !props.webserial &&
                            <button
                                className='settings-button'
                                onClick={reset}
                                disabled={props.isFlashing}>
                                {props.devInfo.type === 'vm' ? 'Reset to Bootloader' : 'Launch vm'}
                            </button>
                        }
                        <br />
                        { props.devInfo.type === 'bootloader' &&
                            <button
                                className='settings-button'
                                onClick={props.onFlashFirmware}
                                disabled={props.isFlashing}>
                                Flash Firmware
                            </button>
                        }
                    </div>
                }
            </div>
        </Box>
    );
}

export default SettingsModalComponent;
