/////////////////////////
//
// Tokenizer
//
/////////////////////////

class Tokenizer {

constructor(s){
this.str = s;
this.offset = 0;
}

readList(){
	var a = [];
	while(true){
		if(this.eof()) break;
		var token = this.readToken();
		if(token==null) break;
		a.push(token);
	}
	return a;
}

readToken(){
	var s = this.next();
	var n = Number(s);
	if(!isNaN(n)) return n;
	var first = s.charAt(0);
	if(first==="]") return null;
	if(first==="[") return this.readList();
	return s;
}

next(){
	if(this.peekChar()==="'") return this.readString();
    else if(this.peekChar()===";") return this.readComment();
	var res='';
	if(this.delim()) res=this.nextChar();
	else {
		while(true){
			if(this.eof()) break;
			if(this.delim()) break;
			else res+=this.nextChar();
	}}
	this.skipSpace();
	return res;
}

readString(){
	this.nextChar();
	var res="'";
	while (true){
		var c=this.nextChar();
		res+=c;
		if(c==="'") {this.skipSpace(); return res;}
		if(this.eof()) return res+"'";
	}
}

readComment(){
	this.nextChar();

}

nextLine(){
	var res='';
	while (true){
		if(this.eof()) return res;
		var c=this.nextChar();
		if(c==='\n') return res;
		res+=c;
	}
}


skipSpace(){
	while(true){
		if(this.eof()) return;
		var c = this.peekChar();
        if(c===';') {this.skipComment(); continue;}
		if(" \t\r\n,".indexOf(c)===-1) return;
		this.nextChar();
	}
}

skipComment(){
	while(true){
		var c = this.nextChar();
		if(this.eof()) return;
		if(c==='\n') return;
	}
}

delim(){return "()[] \t\r\n".indexOf(this.peekChar())!==-1;}
peekChar(){return this.str.charAt(this.offset);}
nextChar(){return this.str.charAt(this.offset++);}
eof() {return this.str.length===this.offset;}

}

export default Tokenizer;
