import strToBytes from './str-to-bytes.js';

class WebHID {
    constructor() {
        this.usbDevice = null
        this.VENDOR_ID = 0x10C4;
        this.PRODUCT_ID = 0xEA80;

        this.waitCallback = null;
        this.waitBuffer = [];
        this.waitFlag = null;
        this.waitLength = 0;

        this.callbackListeners = {
            connect: {},
            disconnect: {},
            receive: {}
        };

        // navigator.hid.addEventListener('connect', async event => {
            // if (!this.isUSBDevice(event.device)) return;
            // this.startUSB(event.device);
        // });

        navigator.hid.addEventListener('disconnect', event => {
            if (!this.isUSBDevice(event.device)) return;
            if (this.usbDevice) {
                this.usbDevice.close();
                this.usbDevice = null;
                Object.keys(this.callbackListeners.disconnect).forEach(key => {
                    this.callbackListeners.disconnect[key]();
                });
            }
        });

        // navigator.hid.getDevices()
        // .then(device => {
            // if (device[0]) this.startUSB(device[0]);
        // });

        this.handleInputReport = this.handleInputReport.bind(this);
    }

    registerListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        this.callbackListeners[type][fcn] = fcn;
    }

    removeListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        delete this.callbackListeners[type][fcn];
    }

    isUSBDevice(dev) {
        return (dev.vendorId === this.VENDOR_ID && dev.productId === this.PRODUCT_ID);
    }

    async searchForUSB() {
        const [device] = await navigator.hid.requestDevice({ filters: [{ vendorId: 0x10c4, productId: 0xea80 }] })
        if (device) this.startUSB(device);
    }

    async startUSB(device) {
        await device.open();
        if (!device.opened) return;
        this.usbDevice = device;
        this.usbDevice.oninputreport = this.handleInputReport;
        await this.enableUSBUART();
        await this.setBaudrate(115200);
        Object.keys(this.callbackListeners.connect).forEach(key => {
            this.callbackListeners.connect[key]();
        });
    }

    async handleInputReport (event) {
        for (var i=0; i<event.data.byteLength; i++) {
            let c = event.data.getUint8(i);
            if (this.waitFlag === c) {
                this.waitCallback();
                this.waitFlag = null;
                this.waitCallback = null;
            } else if (this.waitLength > 0) {
                this.waitBuffer.push(c);
                if (!--this.waitLength) {
                    this.waitCallback([...this.waitBuffer]);
                    this.waitCallback = null;
                    this.waitBuffer = [];
                }
            } else {
                Object.keys(this.callbackListeners.receive).forEach(key => {
                    this.callbackListeners.receive[key](c);
                });
            }
        }
    }

    async getReport (r) {
        const report = await this.usbDevice.receiveFeatureReport(r);
        const data = [];
        for (var i=0; i<report.byteLength; i++) data.push(report.getUint8(i))
        return data;
    }

    async delay (ms) {
        return new Promise(res => {
            setTimeout(res, ms);
        });
    }

    async sendReport (r, b) {
        await this.usbDevice.sendFeatureReport(r, new Uint8Array(b));
    }

    async run () {
        await this.sendData([0xfb]);
    }

    async writeProgram (code) {
        let data = [0xfd, 0xc0, 0x3f, code.length];
        data = data.concat(code);
        await this.sendAndWait(data, 0x4B);
        // let res = await this.sendAndWaitLen([0xfc, 3], 1);
        // console.log('program written');
    }

    async eraseProgram () {
        const data = [0xfd, 0, 0x8c, 64];
        for (let i=0; i<64; i++) data.push(0xFF);
        await this.sendAndWait(data, 0x4b);
        let res = await this.sendAndWaitLen([0xfc, 3], 1);
        console.log(res);
        console.log('memory erased');
    }

    async sendAndWait (data, flag) {
        this.waitFlag = flag;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendStr (str) {
        this.sendData(new Uint8Array(strToBytes(str)));
    }

    async sendAndWaitLen (data, len) {
        this.waitLength = len;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendData (b) {
        if (!this.usbDevice) return;
        for (let i=0; i<b.length; i+=60) {
            const out = b.slice(i, i+60);
            // console.log(`sending ${out}`)
            await this.usbDevice.sendReport(out.length, new Uint8Array(out));
        }
    }

    async readByte (addr) {
        const out = [0xfe, addr&0xFF, (addr>>8)&0xFF];
        const res = await this.sendAndWaitLen(out, 1);
        console.log(res);
    }

    async enableUSBUART () {
        await this.sendReport(0x41, [1]);
    }

    async setBaudrate (b) {
        let out = [];
        for (var i=0; i<4; i++) {
            out.unshift(b&0xFF);
            b >>= 8;
        }
        out = out.concat([0, 0, 3, 0]);
        await this.sendReport(0x50, out);
    }
}

const webhid = new WebHID();
export default webhid;
