import './box.css';

function BoxComponent(props) {
    function stopProp(e) {
        e.stopPropagation();
    }
    return (
        <div
            className='box-frame'
            onClick={props.onClose}>
            <div
                className='box-content'
                onClick={stopProp}>
                {props.children}
            </div>
        </div>
    );
}

export default BoxComponent;
