import './editor.css';

function EditorComponent(props) {
    const padding = props.side === 'left' ? '10px 5px 10px 10px' : '10px 10px 10px 5px';
    const flexStyle = {
        display: 'flex',
        flex: props.flex,
        padding: padding
    };
    return (
        <div className='editor-frame' style={flexStyle}>
            <div className='editor-tools'>{props.tools}</div>
            <div className='editor-text-frame'>
                <textarea
                    ref={props.textRef}
                    className='editor-text'
                    onKeyDown={props.onChange}
                    defaultValue={props.defaultValue}
                    autoCapitalize={'false'}
                    autoCorrect={'false'}
                    autoComplete={'false'}
                    spellCheck={'false'}
                >
                </textarea>
            </div>
        </div>
    );
}

export default EditorComponent;
