import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import GUI from './containers/gui.js';

import { Provider } from 'react-redux';
import { createStore } from 'redux';
import monitorReducer from './reducers/monitors.js';

import { dump, dumpw, writeMemory, readMemory, sendstr } from './lib/vm-commands.js';
import Compiler from './lib/compiler.js';

import WebRTC from './lib/webrtc.js';

window.Compiler = Compiler;
window.dump = dump;
window.dumpw = dumpw;
window.writeMemory = writeMemory;
window.readMemory = readMemory;
window.WebRTC = WebRTC;
window.sendstr = sendstr;

const store = createStore(monitorReducer);

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
        <GUI />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);
