import { useEffect, useState, useContext } from 'react';

import { SettingsContext } from '../contexts/settings.js';

import Comms from '../lib/comms.js';
import { getVersion } from '../lib/vm-commands.js';
import versionParser from '../lib/version-parser.js';

import IconButtonComponent from '../components/icon-button/icon-button.js';

import usbIcon from '../icons/usb.svg';

const USBButton = () => {
    const [isConnected, setIsConnected] = useState(false);
    const {settingsState, settingsActions} = useContext(SettingsContext);

    useEffect(() => {
        Comms.setType(settingsState.deviceType);
        async function onConnect () {
            setIsConnected(true);
            let verString = ''
            const ver = await getVersion();
            for (let i=0; i<ver.length-1; i++) verString += String.fromCharCode(ver[i]);
            const info = versionParser(verString);
            settingsActions.setDeviceInfo(info);
            settingsActions.setConnected();
        }

        function onDisconnect () {
            setIsConnected(false);
            settingsActions.clearDeviceInfo();
            settingsActions.setDisconnected();
        }

        Comms.registerListener('connect', onConnect);
        Comms.registerListener('disconnect', onDisconnect);
        return () => {
            Comms.removeListener('connect', onConnect);
            Comms.removeListener('disconnect', onDisconnect);
        };
    }, [settingsState, settingsActions]);

    function onClick (e) {
        Comms.search();
    }

    return (
        <IconButtonComponent
            title={'Connect to board with USB'}
            src={usbIcon}
            alt={'USB Icon'}
            connected={isConnected}
            handleClick={onClick}
        />
    );
}

export default USBButton;
