import { strToBytes } from './formatting.js';

const MICROVM_UUID = '6e400001-b5a3-f393-e0a9-e50e24dcca9e';
const WRITE_CHAR = '6e400002-b5a3-f393-e0a9-e50e24dcca9e';
const READ_CHAR = '6e400003-b5a3-f393-e0a9-e50e24dcca9e';

class WebBluetooth {
    constructor () {
        this.device = null;

        this.readChar = null;
        this.writeChar = null;
        this.isConnected = false;

        this.waitCallback = null;
        this.waitBuffer = [];
        this.waitFlag = null;
        this.waitLength = 0;

        this.callbackListeners = {
            connect: {},
            disconnect: {},
            receive: {}
        };

        this.onDisconnected = this.onDisconnected.bind(this);
    }

    scan () {
        navigator.bluetooth.requestDevice({filters: [{services: [MICROVM_UUID]}]})
            .then(d => {
                this.device = d;
                this.device.addEventListener('gattserverdisconnected', this.onDisconnected);
                return this.device.gatt.connect();
            })
            .then(server => {
                return server.getPrimaryService(MICROVM_UUID);
            })
            .then(service => {
                service.getCharacteristic(WRITE_CHAR).then(c => {
                    this.writeChar = c;
                });
                service.getCharacteristic(READ_CHAR).then(c => c.startNotifications())
                    .then(c => {
                        c.addEventListener('characteristicvaluechanged', this.handleReadChar.bind(this));
                        this.readChar = c;
                        this.setConnected();
                    });
            })
            // .then(() => {
                // this.setConnected();
            // })
            .catch(error => console.log(error));
    }

    disconnect () {
        this.device.gatt.disconnect();
        this.device = null;
    }

    setConnected () {
        this.isConnected = true;
        Object.keys(this.callbackListeners.connect).forEach(key => {
            this.callbackListeners.connect[key]();
        });
    }

    registerListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        this.callbackListeners[type][fcn] = fcn;
    }

    removeListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        delete this.callbackListeners[type][fcn];
    }

    onDisconnected () {
        this.isConnected = false;
        Object.keys(this.callbackListeners.disconnect).forEach(key => {
            this.callbackListeners.disconnect[key]();
        });
    }

    handleReadChar (event) {
        var input = new Uint8Array(event.target.value.buffer);
        this.processInput(input);
    }

    processInput (data) {
        for (var i=0; i<data.byteLength; i++) {
            let c = data[i];
            if (this.waitFlag === c) {
                this.waitCallback();
                this.waitFlag = null;
                this.waitCallback = null;
            } else if (this.waitLength > 0) {
                this.waitBuffer.push(c);
                if (!--this.waitLength) {
                    this.waitCallback([...this.waitBuffer]);
                    this.waitCallback = null;
                    this.waitBuffer = [];
                }
            } else {
                Object.keys(this.callbackListeners.receive).forEach(key => {
                    this.callbackListeners.receive[key](c);
                });
            }
        }
    }

    async sendAndWait (data, flag) {
        this.waitFlag = flag;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendStr (str) {
        this.sendData(new Uint8Array(strToBytes(str)));
    }

    async sendAndWaitLen (data, len) {
        this.waitLength = len;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendData (data) {
        for (let i=0; i<data.length; i+=20) {
            await this.send(data.slice(i, i+20));
        }
    }

    send (data) {
        if (!this.isConnected) return;
        return new Promise(resolve => {
            // console.log('Sending: ', data);
            var output = new Uint8Array(data);
            this.writeChar.writeValue(output).then(() => {
                resolve();
            });
        });
    }
}

const webbt = new WebBluetooth();
export default webbt;
