import GUIComponent from '../components/gui/gui.js';

import { SettingsProvider } from '../contexts/settings.js';
import { ModalProvider } from '../contexts/modals.js';

import { getSettings } from '../lib/local-settings.js';

function GUI() {
    const savedSettings = getSettings();
    const defaultSettings = {
        auto: false,
        deviceType: 'gio',
        ...savedSettings
    };
    return (
        <SettingsProvider value={defaultSettings}>
            <ModalProvider>
                <GUIComponent />
            </ModalProvider>
        </SettingsProvider>
    );
}

export default GUI;
