import { useEffect, useRef, useContext } from 'react';

import { connect } from 'react-redux';
import { setMonitor } from '../reducers/monitors.js';
import { SettingsContext } from '../contexts/settings.js';

import useVersionParser from '../hooks/use-version-parser.js';

import EditorComponent from '../components/editor/editor.js';

import Comms from '../lib/comms.js';
import Compiler from '../lib/compiler.js';
import { runCmd } from '../lib/vm-commands.js';

function CommandCenter(props) {
    const { settingsActions } = useContext(SettingsContext);
    const defaultText = 'Welcome to LOGO!\n';
    const textRef = useRef(null);

    useEffect(() => {
        let respStr = '';

        function ParseVersion(str) {
            const version = useVersionParser(str);
            settingsActions.setDeviceInfo(version);
        }

        const updateMonitor = (mon, val) => {
          props.updateMonitor(mon, val);
        };

        function handleReceive (c) {
            if (c === 13) {
                if (respStr[0] === '%') console.log(respStr.substr(1));
                else if (respStr[0] === '*') ParseVersion(respStr);
                else if (respStr[0] === '#') updateMonitor(respStr[1], respStr.substr(2));
                else insert(respStr);
                respStr = '';
            } else respStr += String.fromCharCode(c);
        }
        Comms.registerListener('receive', handleReceive);
        return () => {
            Comms.removeListener('receive', handleReceive);
        };
    }, [settingsActions, props]);

    function handleChange (e) {
        if (e.keyCode === 13) {
            if (e.shiftKey) insertCR(e);
            else runCommand(e);
        }
    }

    function runCommand (e) {
        const t = textRef.current;
        const pos = t.selectionStart;
        const start = t.value.lastIndexOf('\n', pos-1);
        let end = t.value.indexOf('\n', pos);
        if (end<0) end = t.value.length;
        t.selectionStart = end+1;
        if (end !== t.value.length) e.preventDefault();
        const str = t.value.substring(start+1, end);
        const code = Compiler.compile(str);
        if (code) runCmd(code);
    }

    function insertCR (e) {
        e.preventDefault();
        const t = textRef.current;
        const pos = t.selectionStart;
        const val = t.value;
        const before = val.substring(0, pos);
        const after = val.substring(pos);
        t.value = before + '\n' + after;
        t.selectionStart = pos + 1;
        t.selectionEnd = pos + 1;
    }

    function insert (str) {
        str += '\n';
        const t = textRef.current;
        const startPos = t.selectionStart;
        const endPos = t.selectionEnd;
        const val = t.value;
        const before = val.substring(0, startPos);
        const after = val.substring(endPos);
        const oldTop = t.scrollTop;
        t.value = before + str;
        const halfScroll = t.scrollHeight - t.scrollTop - t.offsetHeight;
        t.value = before + str + after;
        t.selectionStart = startPos + str.length;
        t.selectionEnd = startPos + str.length;
        if (halfScroll > 0) t.scrollTop += halfScroll;
        else t.scrollTop = oldTop;
    }

    return (
        <EditorComponent
            textRef={textRef}
            flex={props.weight}
            side={props.side}
            onChange={handleChange}
            defaultValue={defaultText}
        />
    );
}

const mapDispatchToProps = dispatch => ({
    updateMonitor: (mon, val) => dispatch(setMonitor(mon, val))
});

export default connect(
    null,
    mapDispatchToProps
)(CommandCenter);
