import './header.css';

import ToolbarMonitorsComponent from '../toolbar-monitors/toolbar-monitors.js';
import IconButtonComponent from '../icon-button/icon-button.js';
import SettingsButton from '../../containers/settings-button.js';
import USBButton from '../../containers/usb-button.js';

import logo from './logo.svg';
import downloadIcon from '../../icons/download.svg';
import loadingIcon from '../../icons/loading.svg';
import stopIcon from '../../icons/stop.svg';

function HeaderComponent(props) {
    return (
        <div className='header-frame'>
            <div
                className='header-title'
            >
                <img src={logo} alt='CraftyBits Logo' draggable='false' />
                CODE
            </div>
            <div className='stretch'>
                <ToolbarMonitorsComponent />
            </div>
            <IconButtonComponent
                src={stopIcon}
                handleClick={props.onStop}
                title={'Send stop command'}
            />
            <SettingsButton />
            <IconButtonComponent
                src={props.isDownloading ? loadingIcon : downloadIcon}
                handleClick={props.onDownload}
                spinning={props.isDownloading}
                title={'Download procedures to device'}
            />
            <USBButton />
            {props.menuVisible &&
                <div className='header-menu'>
                    Flash firmware<br />
                    Test
                </div>
            }
        </div>
    );
}

export default HeaderComponent;
