class WebSerial {
    constructor () {
        this.port = null;
        this.reader = null;
        this.writer = null;
        this.isConnected = false;
        this.onDisconnected = this.onDisconnected.bind(this);

        this.waitCallback = null;
        this.waitBuffer = [];
        this.waitFlag = null;
        this.waitLength = 0;

        this.callbackListeners = {
            connect: {},
            disconnect: {},
            receive: {}
        };
    }

    registerListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        this.callbackListeners[type][fcn] = fcn;
    }

    removeListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        delete this.callbackListeners[type][fcn];
    }

    async scan () {
        try {
            this.port = await navigator.serial.requestPort({
                filters: [{usbVendorId: 0x0d28, usbProductId: 0x0204}]
            });
        } catch (e) { return; }
        await this.port.open({ baudRate: 115200 });
        this.writer = this.port.writable.getWriter();
        this.reader = this.port.readable.getReader();
        if (this.port) {
            this.setConnected();
        }
        try {
            while (true) {
                const { value, done } = await this.reader.read();
                if (value) value.forEach(b => this.processInput(b));
                if (done) {
                    this.reader.releaseLock();
                    break;
                }
            }
        } catch (e) {
            this.onDisconnected();
        }
    }

    processInput (c) {
        if (this.waitFlag === c) {
            this.waitCallback();
            this.waitFlag = null;
            this.waitCallback = null;
        } else if (this.waitLength > 0) {
            this.waitBuffer.push(c);
            if (!--this.waitLength) {
                this.waitCallback([...this.waitBuffer]);
                this.waitCallback = null;
                this.waitBuffer = [];
            }
        } else {
            Object.keys(this.callbackListeners.receive).forEach(key => {
                this.callbackListeners.receive[key](c);
            });
        }
    }

    setConnected () {
        this.isConnected = true;
        Object.keys(this.callbackListeners.connect).forEach(key => {
            this.callbackListeners.connect[key]();
        });
    }

    onDisconnected () {
        this.isConnected = false;
        Object.keys(this.callbackListeners.disconnect).forEach(key => {
            this.callbackListeners.disconnect[key]();
        });
    }

    async sendAndWait (data, flag) {
        this.waitFlag = flag;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendAndWaitLen (data, len) {
        this.waitLength = len;
        return new Promise(res => {
            this.waitCallback = res;
            this.sendData(data);
        });
    }

    async sendData (b) {
        if (!this.writer) return;
        for (let i=0; i<b.length; i+=60) {
            const out = b.slice(i, i+60);
            // console.log(`sending ${out}`)
            this.writer.write(new Uint8Array(out));
        }
    }
}

const webserial = new WebSerial();
export default webserial;
