import WebSerial from './webserial.js';
import WebHID from './webhid.js';
import WebBluetooth from './webbt.js';

import getDeviceSpec from './device-spec.js';

class Comms {
    constructor() {
        this.device = 'gio';
        this.type = 'hid';
        this.commClass = WebHID;
        this.callbackListeners = {
            connect: {},
            disconnect: {},
            receive: {}
        };

        this.commClasses = {
            hid: WebHID,
            serial: WebSerial,
            ble: WebBluetooth
        };

        this.onConnect = this.onConnect.bind(this);
        this.onDisconnect = this.onDisconnect.bind(this);
        this.onReceive = this.onReceive.bind(this);
    }

    setType (type) {
        const spec = getDeviceSpec(type);
        this.device = type;
        this.type = spec.type;

        this.commClass.removeListener('connect', this.onConnect);
        this.commClass.removeListener('disconnect', this.onDisconnect);
        this.commClass.removeListener('receive', this.onReceive);

        this.commClass = this.commClasses[spec.type];

        this.commClass.registerListener('connect', this.onConnect);
        this.commClass.registerListener('disconnect', this.onDisconnect);
        this.commClass.registerListener('receive', this.onReceive);
    }

    onConnect () {
        Object.keys(this.callbackListeners.connect).forEach(key => {
            this.callbackListeners.connect[key]();
        });
    }

    onDisconnect () {
        Object.keys(this.callbackListeners.disconnect).forEach(key => {
            this.callbackListeners.disconnect[key]();
        });
    }

    onReceive (c) {
        Object.keys(this.callbackListeners.receive).forEach(key => {
            this.callbackListeners.receive[key](c);
        });
    }

    registerListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        this.callbackListeners[type][fcn] = fcn;
    }

    removeListener (type, fcn) {
        if (!this.callbackListeners[type]) {
            console.log(`Error: Invalid listener type: ${type}`);
            return;
        }
        delete this.callbackListeners[type][fcn];
    }

    search () {
        if (this.type === 'hid') this.commClass.searchForUSB();
        else this.commClass.scan();
    }

    sendAndWait (data, flag) { return this.commClass.sendAndWait(data,flag); }
    sendAndWaitLen (data, len) { return this.commClass.sendAndWaitLen(data,len); }
}

const comms = new Comms();
export default comms;
