const initialState = {
    settings: false
};

function reducer(state, action) {
    switch(action.type) {
        case 'SET_VISIBLE':
            return { [action.modal]: true }
        case 'SET_HIDDEN':
            return { [action.modal]: false }
        default: return state;
    }
}

export {
    reducer as default,
    initialState
}
